	module.exports = {"data":{"markdownRemark":{"frontmatter":{"title":"МАЛЬЧИК-БАТИСКАФ","type":"lyrics"},"html":"<h1>МАЛЬЧИК&#8208;БАТИСКАФ</h1>\n<section>\n<p>Я научился слышать, как во мне<br>\nжурчит вода, как в этой глубине<br>\nвсё море жидким солнцем обыскав,<br>\nко мне вернулся мальчик‐батискаф.<br>\nОн говорил со мной на языке<br>\nкораллов — он оправдывался мне!<br>\nВ слезах его я камешки искал,<br>\nкоторые он выковал из скал.</p>\n<p>Я голову поймал сетями рук — <br>\nпрыжками рыба рисовала круг<br>\nи неспокойно хлюпала губами — <br>\nрвалась наружу. Превратилась в камень,<br>\nкогда узнала, что искомый клад<br>\nзадавлен не подошвой дна, а над<br>\nводой летает слабым альбатросом,<br>\nглотающим воды тугие тросы<br>\nи рыб крылатых ловящим мешком.<br>\nМой мальчик, научись ходить пешком.</p>\n<p>Теперь я часто слышу, как во мне<br>\nребёнок бегает за птицей по воде.</p>\n</section>"},"site":{"siteMetadata":{"languages":{"defaultLangKey":"en","langs":["en","ru"]}}}},"pathContext":{"slug":"/ru/lyrics/malchikBatiskaf/"}}

